@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Mulish:ital,wght@0,400;0,500;0,600;0,700;0,800;0,900;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
body {
  margin: 0;
  font-family: 'Montserrat', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  /* MAIN FONTS */
  --Font-Main : 'Montserrat', sans-serif;
  --Font-sub : 'Mulish', sans-serif;

  /* MAIN FONT COLORS */
  --Font-Color-Dark : #333333;
  --Font-Color-Dark02 : #4f4f4f;
  --Font-Color-Light : #828282;

  /* COLORS */
  --Main-Color-Red :#EB5757E5;
}

h1 {
  font-family: 'Montserrat', sans-serif;
  font-family: var(--Font-Main);
  font-weight: 700;
  font-size: 45px;
  line-height: 31.26px;
  color: #333333;
  color: var(--Font-Color-Dark);
}

h2 {
  font-family: 'Montserrat', sans-serif;
  font-family: var(--Font-Main);
  font-weight: 700;
  font-size: 24px;
  line-height: 29.26px;
  color: #333333;
  color: var(--Font-Color-Dark);
}

h4, h5 {
  font-size: 16px;
  line-height: 19.5px;
  font-family: 'Montserrat', sans-serif;
  font-family: var(--Font-Main);
  padding: 0;
  margin: 0;
}

p {
  font-size: 14px;
  line-height: 12.07px;
  font-family: 'Montserrat', sans-serif;
  font-family: var(--Font-Main);
  font-weight: 500;
  color: #828282;
  color: var(--Font-Color-Light);
}

.container {
  width: 95%;
  margin: 0 auto;
  padding: 50px 0;
}

.top-texts {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  padding: 0 0 50px 0;
}

.card-row {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: 1fr;
  grid-column-gap: 30px;
  grid-row-gap: 0px;
}

.footer_link {
  text-align: center;
  margin-right: auto;
  margin-left: auto;
  margin-top: 100px;
}

@media (max-width: 800px) {
  .container {
    width: 90%;
    margin: 0 auto !important;
  }

  .header {
    display: block;
  }

  .card-row {
    display: block;
    margin: 0 auto;
    justify-content: center;
    align-items: center;
  }
}


.guest_selector {
    display: block;
    margin-top: 20px;
}

.selector {
    margin-top: 20px;
}
.guest-tab h4 {
    font-size: 14px;
    color: #333;
    font-family: var(--Font-sub);
    padding: 5px 0;
}

.number {
    display: flex;
    align-items: center;
}

.icon_btn {
    height: 23px;
    width: 23px;
    border: none;
    cursor: pointer;
}

.m_ed {
    width: 33px !important;
    height: 28px !important;
}

.number span {
    padding-left: 10px;
    padding-right: 10px;
}
.form_inputs {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 40px;
}

.drawer-box {
    width: 90%;
    height: 460px;
    margin-right: auto;
    margin-left: auto;
}

.form_inputs .location,
.form_inputs .guests,
.form_inputs .search {
    padding: 0px 15px;
    width: 100%;
    height: 55px;
    border-radius: 10px;
    cursor: pointer;
    font-size: 14px;
    line-height: 18px;
    color: #bdbdbd;
    box-shadow: 0px 1px 6px rgba(0, 0, 0, 0.1);
}

.form_inputs .location:hover,
.form_inputs .guests:hover {
    border: 1px solid #333;
}

.search {
   position: relative;
}

.search button {
    margin: 0 auto;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}

.location:focus {
    border: 1px solid #333;
    border-radius: 16px;
}

.location span,
.guests span {
    display: block;
    font-size: 12px !important;
    font-weight: bold;
    color: #333;
}

.location_out {
    font-size: 14px; 
    font-family: 'Mulish, sans-serif'; 
    line-height: 18px;
    margin: 30px auto;
    cursor: pointer;
}

.location_out:hover {
    width: 100%;
    padding: 10px 0;
    background: #ccc;
}

.mobile-search {
    display: none;
}


.edt_search {
    display: none;
}
@media (max-width: 600px) {
    .drawer-box {
        height: 600px;
    }

    .form_inputs {
        display: block;
    }

    .form_inputs .location,
    .form_inputs .guests,
    .form_inputs .search  {
        width: 90%;
        margin-left: auto;
        margin-right: auto;
    }

    .drawer-box {
        padding: 0 20px;
    }

    .form_inputs>div {
        margin-top: 20px;
    }

    .search {
        display: none;
    }

    .search button {
        display: none;
    }
    .mobile-search {
        display: block;
        position: relative;
    }

    .mobile-search button {
        display: flex;
        align-items: center;
        position: absolute;
        text-align: center;
        border: none;
        height: 48px;
        background-color: var(--Main-Color-Red);
        box-shadow: 0px 1px 6px rgba(0, 0, 0, 0.1);
        color: #fff;
        padding: 10px 30px;
        border-radius: 16px;
        cursor: pointer;
        margin-top: 50px;
        top: 50%;
        left: 50%;
        -webkit-transform: translate(-50%, -50%);
                transform: translate(-50%, -50%);
    }

    .edt_search {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-top: 30px;
    }

    .close_icon {
        font-size: 14px !important;
    }
}
.btn_div {
    display: flex;
    align-items: center;
    box-shadow: 0px 1px 6px rgba(0, 0, 0, 0.1);
}

.btn_div button {
    display: block;
    height: 55px;
    background: #fff;
    border: none;
    cursor: pointer;
    color: #bdbdbd;
}

.search_icn {
    position: relative !important;
}

.icn_btn {
    align-self: center;
    text-align: center;
    position: absolute;
    width: 100%;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    color: var(--Main-Color-Red);
}

.input_single  {
    width: 150px;
    border-right: 1px solid #f2f2f2 !important;
}

.guest {
    width: 100px;
    border-right: 1px solid #f2f2f2 !important;
}

.search {
    width: 50px;
    text-align: center;
    color: var(--Main-Color-Red);
}

/* big navigation */
nav .search button {
    border: none;
    background-color: var(--Main-Color-Red);
    color: #fff;
    padding: 10px 30px;
    border-radius: 16px;
    cursor: pointer;
}

nav .location-tab p {
    padding: 10px 0;
    display: flex;
    align-items: center;
}

.location small {
    font-weight: bold;
}

.guests input,
.location input {
    border: none;
    outline: none;
    width: 220px;
}

.overlay {
    position: fixed;
    width: 100%;
    height: 100%;
    background: rgba(40, 40, 40, 0.5);
    top: 0;
    left: 0;
}

@media (max-width: 600px) {
    .btn_div {
        margin-top: 40px;
    }

    .input_single  {
        width: 100%;
    }
    
    .guest {
        width: 100%;
    }
    
    .search {
        width: 100%;
    }
}

.card-item {
    display: block;
    margin-top: 50px;
}

.title-row {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.s_icon {
    font-size: 16px;
    color: var(--Main-Color-Red);
    align-self: center;
    margin-right: 5px;
}

.card-item img {
    max-width: 100%;
    width: 395px;
    height: 269px;
    border-radius: 24px;
}

.super_host {
    padding: 4px;
    font-size: 12px;
    font-weight: 700;
    line-height: 14.63px;
    color: #4f4f4f;
    text-transform: uppercase;
    justify-content: center;
    align-items: center;
    border-radius: 12px;
    box-sizing: border-box;
    border: 2px solid #4f4f4f;
}

@media (max-width: 800px) {
    .title-row {
        justify-content: space-between;
    }
}

